.app-wrapper {
  display: grid;
  grid-template-areas:
          "header header header"
          "nav content content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  width: 1170px;
  margin: 0 auto;
}
.app-wrapper-content {
  grid-area: content;
  background: darkseagreen;
}

